
import command from '../common/Command'

const UNDO_GAP = 2500;

var lastGesture = null;
var lastUndoAt = -1;

function finishUndo() {
    if (lastUndoAt < 0) {
        lastUndoAt = Date.now();
        return;
    }
    if ((Date.now() - lastUndoAt) < UNDO_GAP) {
        //command.undo();
        lastUndoAt = -1;
    } else {
        lastUndoAt = Date.now();
    }
}

export function handleGestureTransient(gesture) {
    if (lastGesture === null) {
        lastGesture = gesture;
        return;
    }

    if (lastGesture.id === "thumb" && gesture.id === "noop") {
        finishUndo();
    }

    lastGesture = gesture;
}

